.App {
  width: 650px;
  text-align: center;
  background-color: black;
  color: white;
  padding: 2rem;
}

.challenge, form, .message {
  padding: 1rem;;
}

.challenge {
  font-size: 2rem;
  font-weight: 500;
  color: green;
}

.challenge span {
  padding: 0 0.5rem;
}

form div {
  width: 300px;
  margin: 0 auto;
  padding: 0.8rem 0;
  display: flex;
  justify-content: space-between;
}

input[type=submit] {
  margin-top: 1rem;
  padding: 0.3rem 0.4rem;
}

